<template>
  <div class="subject_edit">
    <head-top title="课程编辑">
      <span slot="right" @click="saveData">
        <van-icon name="success" class="head-right"></van-icon>
      </span>
    </head-top>
    <van-cell-group>
      <van-field required placeholder="课程名称" maxlength="50" v-model="subjectName" :error-message="subjectNameErr"/>
    </van-cell-group>
  </div>
</template>

<script>
export default {
  name: "subject-edit",
  data () {
    return {
      subjectID: this.$route.params.id || "",
      subjectName: this.$route.params.name || "",
      subjectNameErr: ""
    };
  },
  methods: {
    saveData () {
      this.subjectNameErr = "";
      if (this.subjectName === "") {
        this.subjectNameErr = "请输入课程名称";
        return;
      }
      this.$mainHttp.course.dataAdd({
        courseName: this.subjectName,
        courseID: this.subjectID,
        token: this.$store.getters.token
      }).then(res => {
        if (res.Result) {
          this.$toast("保存成功");
          this.$router.replace("/subject/list");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("保存失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>
